.form-component {
  position: relative;
  min-height: 100vh;

  .form-head {
    background-color: $background-color;
    position: sticky;
    top: $header-height;
    z-index: 5;
    padding-top: $spacing;
    padding-bottom: $spacing-small;

    .base-row-parent {
      border-bottom: $separation-line;
      position: relative;
      cursor: pointer;

      &:hover {
        color: $app-color;
      }

      &::after {
        content: '';
        position:absolute;
        height: 100%;
        width: 100%;
        z-index: 10;
        top: 0;
        left: 0;
        background-color: rgb(240, 240, 240);
        opacity: 0.5;
      }

      &:hover::after {
        opacity: 0;
      }
    }
  }

  .form-container {
    min-height: 100vh;
    position: relative;

    .form-loading-area {
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: 6;
      background-color: rgba(255,255,255, 0.50);

      .loader {
        position: fixed;
        top: 50%;
        left: 66%;
        transform: translateX(-50%);
      }
    }

    .slide-in-form {
      top: 0;
      position: absolute;
    }
  }

  .subtitle {
    color: $font-color-second;
    padding: $spacing;
  }
}

.slide-fade-form-enter-active, .slide-fade-form-move {
  transition: all 0.5s ease;
}
.slide-fade-form-enter, .slide-fade-form-leave-to {
  opacity: 0;
  transform: translateY(-#{$spacing});
}

.slide-fade-form-leave-active {
  position: absolute;
  transition: all 0.3s ease;
}

.slide-child-form-enter-active {
  box-shadow: $pop-up-shadow;
  transition: opacity 0.4s ease-in, transform 0.5s ease-in, box-shadow 0.7s ease-in;
}

.slide-child-form-enter-to {
  box-shadow: none;
}

.slide-child-form-enter {
  transform: translateY(400px);
  opacity: 0;
  box-shadow: $pop-up-shadow;
}

@media screen and (max-width: $mobile) {
  .form-component {
    .form-head {
      padding-top: $spacing-small;
    }

    .form-container {
      .form-loading-area {
        .loader {
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
}
