




























































































































































































































































@import "../styles/variables.scss";
.file-boxes {
  display: flex;
  margin-top: $spacing;

  .file-boxes-margin {
    margin-right: 16px;
  }

  .file-select {
    width: 50%;

    & > input[type='file'] {
      display: none;
    }
  }
}

.file-list {
  margin-top: $spacing;

  & input[type='file'] {
    display: none;
  }

  .mobile-file-list-attach {
    border-top: $separation-line;
  }
}

.menu-wrapper {
  padding: 0 $spacing $spacing;
  background-color: rgb(240, 240, 240);
}

@media screen and (max-width: $mobile) {
  .file-boxes {
    display: none;
  }
}
