.attachment-area {
  .attachment-area-subheader {
    font-size: $font-size-regular;
    color: $font-color-second;
    font-weight: normal;
    margin: $spacing;

  }

  .linked-area {

    .header-row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      .attachment-options {
        display: flex;
      }
    }

    .box-area {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      .linked-base-box {
        cursor: pointer;

        .file-published {
          height: $icon-max;
          width: $icon-max;
          position: absolute;
          border-radius: $icon-max/2;
          background: radial-gradient(closest-side,
            rgba(255,255,255,1) 50%,
            rgba(255,255,255,0) 100%);
          right: -$spacing-small;
          top: -$spacing-small;
          display: flex;

          .published-icon {
            height: $icon-medium;
            max-width: $icon-medium;
            margin: auto;
          }
        }
      }

      .linked-base-box:nth-of-type(n + 5) {
        margin-top: $spacing;
      }

      .linked-base-box:not(:nth-child(4n)) {
        margin-right: $spacing;
      }

      .image-box {
        cursor: pointer;
      }
    }
  }

  .message-area {
    margin-bottom: $spacing-large;
    text-align: center;
    color: $font-color-second;
    backface-visibility: hidden;
    z-index: 1;

    .message-area-text {
      font-size: $font-size-large;
    }

    .message-area-subtext {
      font-size: $font-size-small;
    }

    .license-dropdown {
      margin: $spacing auto 0 auto;
      text-align: left;
    }
  }

  .slide-enter-active {
    transition: all .5s ease-in-out;
  }

  .slide-enter {
    opacity: 0;
    transform: translateY(-#{$spacing});
  }
}

@media screen and (max-width: $mobile) {
  .attachment-area {
    .attachment-area-subheader {
    }

    .linked-area {

      .box-area {

        .linked-base-box {
          flex: 0 0 calc(50% - #{$spacing-small});
        }

        .linked-base-box:nth-of-type(n + 3) {
          margin-top: $spacing;
        }

        .linked-base-box:not(:nth-child(4n)) {
          margin-right: 0;
        }

        .linked-base-box:not(:nth-child(2n)) {
          margin-right: $spacing;
        }
      }
    }
  }
}
