







































































































@import "../styles/variables.scss";

#form-save-button {
  border-left: $separation-line;
}

.form-button-child {
  display: block;
  border-left: $separation-line;
}

.save-loader {
  position: relative;
  transform: scale(0.5);
  margin-right: $spacing;
  padding-right: $spacing;
}

@media screen and (max-width: $mobile) {
  #form-back-button, #form-save-button {
    width: 50%;
  }

  #form-back-button {
    order: 1;
  }

  #form-save-button {
    order: 2;
  }

  .form-button-inner {
    width: 100%;
  }
}
