

















































































































































@import "../styles/variables";

.sidebar-pop-up {
  text-align: center;
  font-size: $font-size-large;
  min-height: 150px;
  max-width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;

  .pop-up-text-container {
    max-width: 100%;

    .sidebar-pop-up-text {
      margin: auto;
      text-align: center;
      overflow-wrap: break-word;
      word-wrap: break-word;
      hyphens: auto;
    }
  }
}

@media screen and (max-width: $mobile) {
  .sidebar-hidden-mobile {
    display: none;
  }
}
