.base-form {
  background-color: white;
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  padding: $spacing $spacing 0;

  .base-form-field {
    margin-bottom: $spacing;
  }

  .base-form-field-full, .group-multiply {
    flex: 0 0 100%;
  }

  .base-form-field-half {
    flex: 0 0 calc(50% - #{$spacing-small});
    width: calc(50% - #{$spacing-small});
  }

  .base-form-field-left-margin {
    margin-left: $spacing;
  }

  .group-multiply {
    margin-bottom: $spacing + $spacing-small;
  }

  .group-add {
    margin-top: $spacing;
  }

  .field-group-button {
    color: $font-color-second;
    cursor: pointer;
    display: inline;
    padding: 0;
    background-color: inherit;
    border: none;

    &:hover {
      color: $app-color;
      fill: $app-color;
    }

    .field-group-icon {
      margin-left: $spacing;
      height: $icon-small;
      width: $icon-small;
    }

    &:focus .field-group-icon {
      fill: $app-color;
    }
  }
}

@media screen and (max-width: $mobile) {
  .base-form {
    padding: $spacing $spacing-small $spacing-small;

    .base-form-field {
      margin-bottom: $spacing-small;
    }

    .base-form-field-half {
      flex: 0 0 100%;
    }

    .base-form-field-left-margin {
      margin-left: 0;
    }

    .group-multiply {
      margin-bottom: $spacing-small + ($spacing-small/2);
    }
  }
}
